/* ==========================================================================
   Author's custom styles
   ========================================================================== */
@import "_modular-scale";

/* font */
$main-font: "Hurme Geometric Sans 4", Helvetica, sans-serif;
$ms-base: 1em;
$ms-ratio: 1.618;
$spacing: 3px;

/* icons */
@import "icon-variables";

@font-face {
    font-family: 'mi-gran-dia_icons';
    src:    url('#{$font-path}/mi-gran-dia_icons.eot?5uwj81');
    src:    url('#{$font-path}/mi-gran-dia_icons.eot?5uwj81#iefix') format('embedded-opentype'),
        url('#{$font-path}/mi-gran-dia_icons.ttf?5uwj81') format('truetype'),
        url('#{$font-path}/mi-gran-dia_icons.woff?5uwj81') format('woff'),
        url('#{$font-path}/mi-gran-dia_icons.svg?5uwj81#mi-gran-dia_icons') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'mi-gran-dia_icons';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    font-size: ms(1);

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-catering {
    &:before {
        content: $icon-catering;
    }
}
.icon-decoracion {
    &:before {
        content: $icon-decoracion;
    }
}
.icon-dulcerias {
    &:before {
        content: $icon-dulcerias;
    }
}
.icon-entretenimiento {
