    }
}
.icon-fotografia {
    &:before {
        content: $icon-fotografia;
    }
}
.icon-invitaciones {
    &:before {
        content: $icon-invitaciones;
    }
}
.icon-joyeria {
    &:before {
        content: $icon-joyeria;
    }
}
.icon-lugares {
    &:before {
        content: $icon-lugares;
    }
}
.icon-mobiliario {
    &:before {
        content: $icon-mobiliario;
    }
}
.icon-musica {
    &:before {
        content: $icon-musica;
    }
}
.icon-organizadores {
    &:before {
        content: $icon-organizadores;
    }
}
.icon-otros {
    &:before {
        content: $icon-otros;
    }
}
.icon-pastelerias {
    &:before {
        content: $icon-pastelerias;
    }
}
.icon-pinatas {
    &:before {
        content: $icon-pinatas;
    }
}
.icon-presentadores {
    &:before {
        content: $icon-presentadores;
    }
}
.icon-recuerdos {
    &:before {
        content: $icon-recuerdos;
    }
}
.icon-regalos {
    &:before {
        content: $icon-regalos;
    }
}
.icon-salon {
    &:before {
