/* color palettte */
$green: #aed136;
$pink: #ee3470;
$purple: #8c3e96;
$orange: #faa41a;
$gray-text: #818181;
$gray-bg: #cecece;
$gray-highlight: #dadada;
$gray-form: rgba(128, 128, 128, 0.3);
$gray-form-text: #666;
$white: #fff;
$dark-overlay: rgba(0, 0, 0, 0.7);

/* box */
$std-padding: 15px;

$radius: 3px;

body {
    font-family: $main-font;
    padding: 124px 0 0;
    margin: 0;
}

.gray-bg {
    background: $gray-highlight;
}

a {
    text-decoration: none;
}

nav .container {
    padding: 0;
}

.navbar > .container .navbar-brand, .navbar > .container-fluid .navbar-brand {
    margin-top: 15px;
    padding: 0;
}

.logo {
    width: 100%;
}

.navbar {
    margin: 0;
    background: #fff;
}

.navbar-fixed-top {
    border: 0;
}

nav.navbar.navbar-fixed-top {
    height: 124px;
}

nav.navbar {
    padding: 0 15px;
    color: $gray-text;
}

div#userNavbar {
    position: relative;
    top: -124px;
}

div#userNavbar li a {
    line-height: 10px;
    background: none;
    text-transform: none;
}

div#userNavbar li:hover a {
    background: $green;
    color: $white;
}

div#mainNavbar li a {
    line-height: 94px;
    background: none;
}

#mainNavbar span.caret {
    border-top: 20px dashed #fff;
    border-right: 20px solid transparent;
    border-left: 20px solid transparent;
    left: 33%;
    top: -1px;
    position: relative;
}

.nav li a {
    font-family: $main-font;
    font-size: ms(0);
    font-weight: normal;
    letter-spacing: $spacing;
    text-transform: uppercase;
    line-height: 4.236em;
    padding: 15px 22px;
    color: $gray-text;
}

.nav li a:hover {
    background: $white;
    color: $pink;
}

.navbar-toggle {
    margin: 0;
}

.container-fluid {
    padding: 0;
}

.item {
    height: 600px;
    width: 100%;

    img {
        width: 100%;
    }
}

.carousel-caption {
    font-family: $main-font;
    font-size: ms(3);
    font-weight: 900;
    text-transform: uppercase;
    top: 35%;
    padding-bottom: 0;
    line-height: 95%;
}

.jumbotron {
    padding: 0 !important;
}

.col-md-6 {
    padding: 0;
}

.btn {
    font-family: $main-font;
    font-weight: 500;
    font-size: ms(1);
    letter-spacing: $spacing;
    text-shadow: none;
    text-transform: uppercase;
    color: $white;
    background: none;
    background-image: none;
}

.btn-default {
    background: none;
    box-shadow: none;
    border: 0;
    border-radius: 0;
}

.btn-lg {
    width: 33.333333333%;
    background: #cecece;
    height: 110px;
    margin: 0;
    padding: 0;
    float: left;
    color: #666;
}

.home-cat {
    background: $purple;
    position: relative;
    overflow: hidden;
    min-height: 300px;
    text-align: center;
    width: 33.33333%;

    h5 {
        font-size: ms(1);
    }

    &:nth-child(odd) {
        background: $orange;
    }

    :before {
        content: ' ';
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        opacity: 0.1;
        background-image: url("../img/mgd_home_categories_bg.jpg");
        background-repeat: no-repeat;
        background-position: 50% 0;
        -ms-background-size: cover;
        -o-background-size: cover;
        -moz-background-size: cover;
        -webkit-background-size: cover;
        background-size: cover;
    }
}

.separator {
    min-height: 30px;
    border-top: $gray-bg 1px solid;
}


.drawer-icon {
    width: 30%;
}

.drawer-menu {
    background: $dark-overlay;
    top: -40px;
    position: relative;

    h5 {
        color: #fff !important;
    }

    .btn-lg {
        background: transparent;
        width: 16.666666666%;
    }

    .btn-lg:nth-child(odd) {
        background: transparent;
    }
}

.btn-lg:nth-child(odd) {
    background: $gray-bg;
}

.btn-lg:hover {
    color: $white;
    background: $green;
}

.cat-menu {
    display: block;
    bottom: 110px;
    margin-top: -140px;
}

.btn-group,
.btn-group-vertical {
    width: 100%;
    line-height: 2.618em;
}

.btn-group .btn + .btn {
    margin-left: 0;
}

.btn-group > .btn:first-child:not(:last-child):not(.dropdown-toggle) {
    background: rgba(0, 0, 0, 0.2);
    width: 15%;
    border: 0;
    border-radius: 0;
    line-height: 67px;
}

.btn-group > .btn:last-child:not(:first-child),
.btn-group > .dropdown-toggle:not(:first-child) {
    text-align: left;
    width: 85%;
    border: 0;
    border-radius: 0;
    line-height: 67px;
}

input[type="checkbox"] {
        display: none;
}

label.btn.btn-default {
    width: 100%;
    padding: $std-padding;

    span {
        margin-right: 10px;
    }

    &.active {
        background: $purple;
        color: $white;  
    }
}

.menu-icon {
    width: 20%;
    max-width: 80px;
    max-height: 60px;
    margin-right: 10px;
}

.cat-icon {
    width: 25%;
}

.first-row {
    background: $purple;
}

.second-row {
    background: $pink;
}

.third-row {
    background: $orange;
}

.fourth-row {
    background: $green;
}

footer {
    font-family: $main-font;
    font-weight: 500;
    font-size: ms(0);
    letter-spacing: $spacing;
    text-transform: uppercase;
    color: $gray-text;
    background: $gray-bg;
    padding: 15px;
    margin: 0;

    ul {
        padding: 0;
    }

    li {
        list-style: none;
    }

    li a {
        color: $gray-text;
    }
}

@media only screen and (max-width: 1024px) {
    .nav li a {
        font-size: .9em;
    }
}

@media only screen and (max-width: 768px) {
    .container {
        width: 100% !important;
    }

    .container > .navbar-collapse, .container > .navbar-header {
        margin-right: -15px;
        margin-left: -15px;
    }

    .navbar > .container .navbar-brand, .navbar > .container-fluid .navbar-brand {
        margin-left: 0 !important;
    }

    div#mainNavbar li a, div#userNavbar li a {
        font-size: ms(-1);
        line-height: 30px;
        background: none;
        text-transform: uppercase;
    }

    div#mainNavbar {
        padding-top: 90px;
    }

    div#userNavbar {
        position: inherit;
        top: none;
    }

    .btn-group > .btn:first-child:not(:last-child):not(.dropdown-toggle) {
        width: 25%;
    }

    .btn-group > .btn:last-child:not(:first-child), .btn-group > .dropdown-toggle:not(:first-child) {
        font-size: ms(1);
        width: 75%;
    }

    .first-row .col-md-6:nth-child(even) {
        background: $orange;
    }

    .second-row .col-md-6:nth-child(even) {
        background: $green;
    }

    .third-row .col-md-6:nth-child(even) {
        background: $purple;
    }

    .fourth-row .col-md-6:nth-child(even) {
        background: $pink;
    }

    img.img-responsive.center-block {
        height: 100%;
    }

    .item img {
        width: auto !important;
    }

    .carousel-inner > .item > img, .img-responsive {
        max-width: none;
    }

    img.img-responsive.center-block {
        position: relative;
    }

    .carousel-caption {
        font-size: ms(2);
    }

    .navbar-toggle .icon-bar {
        display: block;
        width: 48px;
        height: 7px;
        border-radius: 2px;
        background: #818181;
    }

    .navbar-toggle {
        display: none;
        background-color: #dadada;
        border: 1px solid transparent;
        border-radius: 4px;
        height: 68px;
        width: 70px;
        float: right;
        margin-top: 15px;
    }

    .navbar-brand {
        margin-left: 0 !important;
    }

    #mainNavbar li, #userNavbar li {
        width: 50%;
        text-align: center;
        background: #fff;
        font-size: ms(1);
        float: left;
    }

    .navbar-nav {
        margin: 0;
        padding: 0;
    }

    .navbar-collapse {
        padding: 0;
        border: 0;

        .navbar-right {
            float: none !important;
        }
    }

    .container > .navbar-header {
        width: 100%;
        padding: 0;
        margin: 0;
    }

    .logo {
        width: 72%;
    }

    body {
        padding: 263px 0 0;
    }
}

@media only screen and (max-width: 767px) {
    .navbar-toggle {
        display: block;
    }

    body {
        padding: 96px 0 0;
    }

    div#mainNavbar {
        padding-top: 0;
    }
}

@media only screen and (max-width: 425px) {
    .logo {
        width: 256px !important;
    }

    body {
        padding: 96px 0 0;
    }

    button h5 {
        font-size: ms(-2);
    }

    .btn-group > .btn:first-child:not(:last-child):not(.dropdown-toggle) {
        width: 25%;
    }

    .btn-group > .btn:last-child:not(:first-child), .btn-group > .dropdown-toggle:not(:first-child) {
        font-size: ms(0);
        width: 75%;
    }

    nav.navbar.navbar-fixed-top {
        height: 98px;
    }
}

@media only screen and (max-width: 375px) {
    body {
        padding: 96px 0 0;
    }

    .logo {
        width: 250px !important;
    }

    nav.navbar.navbar-fixed-top {
        height: 96px;
    }

    .navbar-toggle {
        margin-top: 14px;
        margin-right: 0;
    }
}

@media only screen and (max-width: 320px) {
    .logo {
        width: 190px !important;
    }

    .navbar-toggle {
        position: relative;
        float: right;
        padding: 10px;
        margin-top: 14px !important;
        margin-right: 0 !important;
        margin-bottom: 8px !important;
        background-color: #dadada;
        border: 1px solid transparent;
        border-radius: 4px;
        height: 52px;
        width: 52px;
    }

    .navbar-toggle .icon-bar {
        display: block;
        width: 30px;
        height: 5px;
        border-radius: 2px;
        background: #818181;
    }

    nav.navbar.navbar-fixed-top {
        height: 80px;
    }

    body {
        padding: 80px 0 0;
    }

    #mainNavbar li, #userNavbar li {
        width: 50%;
        text-align: center;
        background: #fff;
        font-size: 0.618em;
        line-height: .618em;
    }

    .nav li a {
        font-family: "Hurme Geometric Sans 4", Helvetica, sans-serif;
        font-size: ms(0);
        font-weight: normal;
        letter-spacing: 3px;
        text-transform: uppercase;
        color: #818181;
    }

    div#mainNavbar li a, div#userNavbar li a {
        font-size: ms(0);
        line-height: 30px;
        background: none;
        text-transform: uppercase;
    }

    .btn-group > .btn:last-child:not(:first-child), .btn-group > .dropdown-toggle:not(:first-child) {
        font-size: ms(0);
        text-align: left;
        width: 75%;
        border: 0;
        border-radius: 0;
        height: 100%;
    }

    .btn-group > .btn:first-child:not(:last-child):not(.dropdown-toggle) {
        background: rgba(0, 0, 0, 0.2);
        width: 25%;
        border: 0;
        border-radius: 0;
    }

    .carousel-caption {
        font-size: ms(1);
        top: 25%;
        padding-bottom: 0;
        line-height: 105%;
    }

    .item {
        height: 400px;
        width: 100%;
    }
}

/******** Login *********/

.login-caption h1 {
    font-weight: 900;
}

.img-panel {
    background-size: cover;
    background-position: center;
    text-align: center;
    min-height: 600px;
}

.img-panel-full {
    background-size: cover;
    background-position: center;
    text-align: center;
    min-height: 400px;
    position: relative;
}

.img-panel-full-register {
    background-image: url("../img/mi_gran_dia_cumpleanos.jpg");
}

.img-proveedor {
    background-image: url("../img/mi_gran_dia_conferencia.jpg");
}

.img-usuario {
    background-image: url("../img/mi_gran_dia_cumpleanos.jpg");
}

.login-caption {
    font-family: $main-font;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: $spacing;
    color: $gray-text;
    text-align: center;
    padding: 15px 0;
}

.login-caption a {
    color: $pink;
}

.login-caption a:hover {
    color: $gray-text;
}

.login-form {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background: $white;
    padding: 15% 0;
    min-height: 600px;
}

.form-control {
    height: 46px;
    padding: 26px 20px;
    margin: 8px 0;
    font-size: ms(1);
    letter-spacing: $spacing;
    color: $pink;
    text-transform: uppercase;
    background: $gray-form;
}

label {
    font-size: ms(1);
    font-weight: 500;
    letter-spacing: $spacing;
    color: $pink;
    text-transform: uppercase;
}

.row.login {
    margin: 0;
}

.btn-submit {
    background: $green;
    color: $white;
    margin: 10px 0;
    font-size: ms(1);
}

.form-group {
    font-family: $main-font;
    font-weight: 400;
    margin: 0;
}

@media only screen and (max-width: 1024px) {
    .login-form {
        min-height: 600px;
    }

    .login-form .form-horizontal {
        padding: 0 40px;
    }
}

@media only screen and (max-width: 425px) {
    .form-horizontal .form-group {
        margin-right: 20px;
        margin-left: 20px;
    }

    .form-control {
        margin: 8px 0;
        font-size: ms(0);
    }

    .img-panel {
        min-height: 200px;
    }

    .login-form {
        padding: 5% 0;
        min-height: 400px;
    }

    .login-form .form-horizontal {
        padding: 0 10px;
    }
}

.btn-file {
    background: $green;
    font-size: 16px;
}

/******** Register ************/

.kv-avatar .file-preview-frame, .kv-avatar .file-preview-frame:hover {
    margin: 0;
    padding: 0;
    border: none;
    box-shadow: none;
    text-align: center;
}

.kv-avatar .file-input {
    display: table-cell;
    max-width: 220px;
}

.file-preview {
    min-height: 275px;
}

.file-default-preview img {
    width: 100%;
}

.bfh-selectbox .bfh-selectbox-toggle .bfh-selectbox-option {
    display: inline-block;
    float: left;
    width: 100%;
    height: 100% !important;
    overflow: hidden;
    text-overflow: ellipsis;
}

.bfh-selectbox.open > .bfh-selectbox-options {
    width: 100%;
}

.bfh-selectbox .bfh-selectbox-options ul {
    max-width: none;
    width: 100%;
}

.register-form {
    margin-top: 15px;

    .form-control {
        padding: 6px 20px;
    }

    .form-horizontal > .col-lg-4, .form-horizontal > .col-md-4, .form-horizontal > .col-lg-8, .form-horizontal > .col-md-8 {
        padding: 0;
    }
}

.btn.btn-primary.btn-file {
    height: 46px;
    padding-top: 12px;
    margin: 0;
    border: 0;
}

.btn.btn-primary.btn-file:hover {
    background: $gray-bg;
    color: $gray-text;
}

.fileinput-remove, .fileinput-upload {
    font-size: 16px;
    background: $green;
}

.file-caption-name {
    font-size: ms(-1);
}

.form-horizontal .form-group {
    margin-right: 10px;
    margin-left: 10px;
}

a.btn.btn-default.fileinput-upload.fileinput-upload-button {
    height: 46px;
    padding-top: 12px;
}

button.btn.btn-default.fileinput-remove.fileinput-remove-button {
    height: 46px;
}

.input-group-btn {
    position: relative;
    font-size: 0;
    white-space: nowrap;
    top: 4px;
}

/************ Register Evento ***************/
h1.promt {
    text-transform: uppercase;
    color: $purple;
    font-weight: 700;
}

.register-panel {
    .btn.btn-default {
        font-size: ms(0);
        background: $gray-bg;
        color: $gray-text;
        border-radius: $radius !important;
        text-align: left;
    }

    .btn-group > .btn:last-child:not(:first-child), .btn-group > .dropdown-toggle:not(:first-child) {
        width: 73%;
        margin-left: 2%;
    }

    .btn-group > .btn:first-child:not(:last-child):not(.dropdown-toggle) {
        width: 23%;
        margin-right: 2%;
    }

    .col-md-6:hover > .btn-group .btn.btn-default, .col-md-4:hover > .btn-group .btn.btn-default {
        background: $purple;
        color: $white;
    }

    .col-md-6, .col-md-4 {
        padding: 10px 15px;
        border-radius: 3px;
    }

    .next {
        margin-left: .5%;
        background: $green !important;
        color: $white !important;
    }

    .prev {
        margin-right: .5%;
    }
}

.account {
    background: $white;
    border-radius: $radius;
    padding: 15px 20px;
    text-align: center;
    border: solid 1px $gray-bg;

    h2 {
        font-family: $main-font;
        font-weight: 700;
        font-size: ms(1);
        letter-spacing: $spacing;
        text-shadow: none;
        text-transform: uppercase;
        color: $pink;
        background: none;
        background-image: none;
    }

    ul {
        margin-top: 10px;

        li {
            color: $gray-text;
            font-size: ms(0);
            line-height: 40px;
        }

        .check {
            list-style-image: url("../img/check.png");
            margin-bottom: 0;
        }

        .uncheck {
            list-style-image: url("../img/uncheck.png");
        }
    }
}

.pattern {
    background: url("../img/mi_gran_dia_pattern.png") repeat;
}

.main {
    padding: 30px 0;
    min-height: 750px;
}

.icon {
    margin: 5px;
}

.modal-welcome {
    padding: 20px;
    color: $white;
    line-height: 40px;
    font-size: ms(1);
    background: url("../img/mi_gran_dia_welcome.png") no-repeat, $purple;
    background-size: cover;

    h1 {
        font-size: ms(2);
        text-transform: uppercase;
        font-weight: 900;
        margin: 20px 0;
        -webkit-text-stroke: 2px #732a7f;
        text-shadow: 0 1px 0 #732a7f, -1px 2px 0 #732a7f, -2px 3px 0 #732a7f, -3px 4px 0 #732a7f, -4px 5px 0 #732a7f, -5px 6px 0 #732a7f, -6px 7px 0 #732a7f, -7px 8px 0 #732a7f, -8px 9px 0 #732a7f, -9px 10px 0 #732a7f, -10px 11px 0 #732a7f;
    }

    > .btn-submit {
        font-size: ms(-1);
        float: none;
        margin: 35px;
    }
}

@media only screen and (max-width: 425px) {
    .modal-welcome h1 {
        font-size: ms(1);
    }
}

/******* Seller Profile ******/
.supplier {
    color: $gray-text;
    letter-spacing: $spacing;

    .frame {
        border: $gray-bg solid 1px;
        border-radius: $radius;
        background: $white;
        margin: $std-padding 0;
        padding: $std-padding*1.333;

        h1, h2, h3 {
            font-size: ms(1);
            padding: 10px 0;
            margin: 0;
        }

    }

    .btn-submit {
        font-size: ms(0);
    }
    .gallery {
        top:20px;
    }
    .swiper-container {
        width: 100%;
        height: 300px;
        margin-left: auto;
        margin-right: auto;
    }

    .swiper-slide {
        background-size: cover;
        background-position: center;
    }

    .gallery-top {
        height: 600px;
        width: 100%;
    }

    .gallery-thumbs {
        height: 150px;
        box-sizing: border-box;
        padding: 10px 0;
    }

    .gallery-thumbs .swiper-slide {
        width: 15%;
        height: 100%;
        opacity: 0.4;
    }

    .gallery-thumbs .swiper-slide-active {
        opacity: 1;
    }

    .avatar {
        width: 100%;
        padding: 20px;
        max-width: 250px;
    }

    h1.name {
        font-size: ms(2);
        font-weight: 900;
        text-transform: uppercase;
        color: $pink;
    }

    .check {
        list-style-image: url("../img/check.png");
        margin-bottom: 0;
    }

    .highlights, .description {
        margin: 15px 0;
    }

    ul {
        padding: 0;
        .menu-item {
            float: left;
            display: inline-block;
            width: 25%;
            text-align: center;
            background: $gray-bg;
            padding: 10px;
            
            &:hover {
                background: $green;
            }

            a {
                font-size: ms(0);
                color: $white;  
                text-transform: uppercase;
                text-decoration: none;
            }
        }
    }

}

@media only screen and (max-width: 1024px) {
        h2.address {
        font-size: 1.2em;
    }

}

@media only screen and (max-width: 425px) {
    .col-md-2.col-md-offset-1.col-sm-4.col-sm-offset-1, .col-md-3.col-sm-6 {
        text-align: center;
    }
}